@import "Transforms";

.body {
  width: 100%;
  height: 100%;
  background: #35393c;
  border-radius: 20px;
}

.coder {
  width: 100%;
  height: 100%;
  background: #35393c;
  border-radius: 20px;
}

.editor {
  position: absolute;
  top: 20px;
  left: 20px;
  right: 20px;
  bottom: 20px;
  color: #c5c5c5;
}

#code {
  position: absolute;
  top: 20px;
  left: 20px;
  right: 20px;
  bottom: 20px;
  color: #c5c5c5;
}

.code {
  // display: block;
  min-width: 100%;
  min-height: 100%;
  position: absolute;
}

.precode {
  position: relative;
  height: 100%;
  width: 100%;
}

.extended {
  position: absolute;
  width: 100%;
  height: 100%;
  @include transform(translate(15px, -45px)) border-radius: 20px;
  background: #2b2f32;
  z-index: -1;
}

.selector {
  position: absolute;
  top: -40px;
  width: 200px;
  left: 6px;
  @include transform(scale(0.7));
}
