html, body {
  width: 100%;
  height: 100%;
}

.body {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-family: 'Montserrat' !important;
}

.center {
  max-width: 50%;
  align-self: center;
  align-items: center;
  justify-content: center;
  text-align: center;
}

.text {
  color: #35393C;
}

.return {
  background: #4598d3;
  box-sizing: border-box;
  border: none;
  padding: 10px 10px;
  border-radius: 5px;
  cursor: pointer;
  user-select: none;
  transition: 0.2s;
  &:hover {
    background: #3a83b8;
  }
  a {
    text-decoration: none;
    color: #fffffa;
  }
}
