@import url('https://fonts.googleapis.com/css?family=Montserrat:400,600,700|Work+Sans:300,400,700,900');

.nav{
  background: transparent;
  margin: 0;
  padding: 0;
  display: relative;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  font-family: 'Montserrat';
  .name {
    position: relative;
    top: 20px;
    padding: 20px 20px;
    font-size: 20px;  
  }
}

.navbar {
  margin: 30px 50px;
  padding: 0;
  float: right;
  display: flex;
  .item {
    list-style: none;
    margin: 0 20px;
    transition: 0.5s;
    a {
      display: block;
      position: relative;
      text-decoration: none;
      padding: 5px;
      font-size: 18px;
      color: #35393C;
      text-transform: uppercase;
      transition: 0.5s;
      &:hover {
        transform: scale(2);
        opacity: 1;
        filter: blur(0);
      }
      &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: #4598d3;
        transition: 0.5s;
        transform-origin: right;
        transform: scaleX(0);
        z-index: -1;
      }
      &:hover:before {
        transform: scaleX(1);
      }
    }
  }
  &:hover li a{
    transform: scale(1.5);
    opacity: 0.2;
    filter: blur(2px);
  }
}
