@import 'Transforms';
@import url('https://fonts.googleapis.com/css?family=Montserrat:400,600,700|Work+Sans:300,400,700,900');

html, body {
  width: 100%;
  height: 100%;
  overflow-x: hidden;
}

.body {
  background: #fffffa;
}

.nav {
  height: 100px;
  width: 100%;
}

img.jssplash {
  width: 400px;
}

div.jssplash {
  position: absolute;
  right: 40px;
  top: 10px;
  height: 400px;
  width: 400px;
  @include transform(rotate(-20deg) scale(1.5));
  .jssplashtext {
    position: relative;
    height: 50px;
    width: 100%;
    a {
      font-size: 18px;
      font-family: 'Montserrat';
      text-decoration: none;
      color: #35393C;
      .under {
        text-decoration: underline;
      }
    }
  }
}

img.pysplash {
  width: 400px;
}

div.pysplash {
  position: absolute;
  left: -40px;
  top: 500px;
  height: 400px;
  width: 400px;
  @include transform(rotate(20deg) scale(1.2));
  .pysplashtext {
    position: relative;
    height: 50px;
    width: 100%;
    a {
      font-size: 18px;
      font-family: 'Montserrat';
      text-decoration: none;
      color: #35393C;
      .under {
        text-decoration: underline;
      }
    }
  }
}

.editorpadding {
  position: absolute;
  top: 50px;
  left: 350px;
  right: 550px;
  height: 1000px;
  margin: 100px;
  .editor {
    width: 100%;
    height: 100%;
  }
}
